<template>
  <div class="cm_select">
    <div v-if="type == 'type1'" class="input_box input_box1">
      <div class="title">{{ title }}</div>
      <van-cell-group>
        <van-field
          v-model="fieldValue"
          is-link
          readonly
          label=""
          placeholder="请选择"
          @click="show = true"
        />
        <van-popup v-model="show" round position="bottom">
          <van-cascader
            v-model="cascaderValue"
            :title="title"
            :options="options"
            @close="show = false"
            @finish="onFinish"
          />
        </van-popup>
      </van-cell-group>
    </div>
    <div v-if="type == 'type2'" class="input_box input_box1  input_box2">
      <div class="title">{{ title }}</div>
      <van-cell-group>
        <van-field
          v-model="fieldValue"
          is-link
          readonly
          :label="label"
          placeholder="请选择"
          @click="show = true"
        />
        <van-popup v-model="show" round position="bottom">
          <van-cascader
            v-model="cascaderValue"
            :title="title"
            :options="options"
            @close="show = false"
            @finish="onFinish"
          />
        </van-popup>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "type1"
    },
    label: {
      type: String,
      default: ""
    },
    value: {
      trye: String,
      default: ""
    },
    options: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      show: false,
      fieldValue: this.value,
      cascaderValue: ""
    };
  },
  methods: {
    onFinish({ selectedOptions }) {
      this.show = false;
      this.fieldValue = selectedOptions.map(option => option.text).join("/");
      this.$emit("select", selectedOptions);
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
/* @import url(); 引入css类 */
.cm_select {
  width: 100%;
}
.input_box {
  width: 100%;
  margin-top: 61px;
  padding-bottom: 38px;
  border-bottom: 2px solid #ebebeb;
}
.input_box1 {
  margin-top: 44px;
  .title {
    border-left: 6px solid #4f82fa;
    padding-left: 16px;
    margin-bottom: 40px;
  }
}
#app .cm_select .input_box .title {
  font-size: 30px;
  font-weight: bold;
}
/deep/ {
  .van-cell {
    padding: 0;
    font-size: 30px;
    .van-field__label {
      color: #252525;
      font-weight: 500;
    }
    .van-field__control {
      text-align: left;
    }
  }
  .input_box1 {
    .van-field__label {
      color: #bababa;
    }
    .van-icon-clear {
      font-size: 34px;
      &::before {
        content: url("../assets/imgs/close.png");
      }
    }
  }
  .input_box2 {
    .van-field__label {
      color: #252525;
    }
    .van-field__label {
      line-height: 40px;
    }
  }
  .van-cascader__title,
  .van-tab,
  .van-cascader__option {
    font-size: 30px;
    line-height: 46px;
  }
  .van-icon-success {
    font-size: 30px;
  }
  .van-cascader__tabs .van-tabs__nav--complete {
    display: none;
  }
  .van-icon {
    font-size: 30px;
  }
  .van-cell__right-icon {
    margin-top: 11px;
  }
}
</style>



