<template>
  <div class="cm_input">
    <div v-if="type == 'type1'" class="input_box">
      <van-cell-group>
        <van-field
          @input="change"
          v-model="val"
          :label="label"
          placeholder=""
        />
      </van-cell-group>
    </div>

    <div v-if="type == 'type2'" class="input_box input_box2">
      <div class="title">{{ title }}</div>
      <van-cell-group>
        <van-field
          @input="change"
          :clearable="true"
          @clear="clear"
          :left-icon="waringIcon"
          clear-trigger="focus"
          v-model="val"
          :placeholder="label"
          :formatter="formattier"
          :error="err"
          :required="err"
        />
      </van-cell-group>
    </div>

    <div v-if="type == 'type3'" class="input_box input_box2 input_box3">
      <van-cell-group>
        <van-field
          @input="change"
          :clearable="true"
          @clear="clear"
          clear-trigger="focus"
          v-model="val"
          :label="title"
          :placeholder="label"
        />
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "type1"
    },
    label: {
      type: String,
      default: ""
    },
    value: {
      trye: String,
      default: ""
    }
  },
  data() {
    return {
      val: this.value,
      err: false,
      errMessage: "请输入",
      waringIcon: ""
    };
  },
  methods: {
    clear() {
      this.val = "";
    },
    change(value) {
      if (value) {
        console.log(value);
        this.$emit("update", value);
      }
    },
    formattier(val) {
      if (!val) {
        this.errMessage = "请输入";
        this.err = true;
        this.waringIcon = "warning-o";
      } else {
        this.err = false;
        this.waringIcon = "eeeee";
      }
      return val;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
/* @import url(); 引入css类 */
.cm_input {
  width: 100%;
}

.input_box {
  width: 100%;
  margin-top: 61px;
  padding-bottom: 38px;
  border-bottom: 2px solid #ebebeb;
  .title {
    font-size: 30px;
    font-weight: bold;
  }
}
.input_box2 {
  margin-top: 44px;
  .title {
    border-left: 6px solid #4f82fa;
    padding-left: 16px;
    margin-bottom: 40px;
  }
}
/deep/ {
  .van-cell {
    padding: 0;
    font-size: 30px;
    .van-field__label {
      line-height: 40px;
      font-weight: 500;
      color: #252525;
      width: 5em;
      white-space: nowrap;
    }
    .van-field__control {
      text-align: right;
    }
  }
  .input_box2 {
    .van-icon-warning-o {
      font-size: 30px;
      position: relative;
      top: 9px;
      margin-right: 10px;
      color: #ed5e58;
    }
    .van-field__control {
      text-align: left;
    }
    .van-field__label {
      color: #bababa;
    }
    .van-icon-clear {
      font-size: 34px;
      &::before {
        content: url("../assets/imgs/close.png");
      }
    }
  }
  .input_box3 {
    .van-field__label {
      color: #252525;
      line-height: 40px;
    }
  }
  .van-cell--required::before {
    content: "";
  }
}
</style>



